import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/query/react";
import {URL_SERVER} from "../consts/settings";
import {IRequestAR, IResponseAR} from "../models/IARService";

export const arApi = createApi({
    reducerPath: "arApi",
    baseQuery: fetchBaseQuery({baseUrl: URL_SERVER, headers:{  accept: "application/json",
            "Accept-Language": "en-US,en;q=0.8",
            "Content-Type": "multipart/form-data;",

        }}),
    endpoints: (build) => ({
        getPositionByImage: build.mutation<IRequestAR, FormData>({
            query: (body) => ({
                url: "",
                method: "POST",
                body: body,
                mode:"cors",

            })
        })
    })
})

export const {useGetPositionByImageMutation} = arApi