import {combineReducers, configureStore} from "@reduxjs/toolkit"
import {arApi} from "../services/ARService";
import applicationReducer from "./AppicationSlice"
import sceneThree from "./SceneThreeSlice";

const rootReducer = combineReducers({
    applicationReducer,
    sceneThree,
    [arApi.reducerPath]: arApi.reducer
})

export const setupStore = () => {
    return configureStore({
        reducer: rootReducer,
        middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(arApi.middleware)
    })
}

export type RootState = ReturnType<typeof rootReducer>
export type AppStore = ReturnType<typeof setupStore>
export type AppDispatch = AppStore['dispatch']