import {OBJLoader} from "three/examples/jsm/loaders/OBJLoader";
import {MTLLoader} from "three/examples/jsm/loaders/MTLLoader.js"
import * as THREE from "three";
import {setupStore} from "../../store/store"

export async function loadObj(path: string, name: string, material: THREE.Material): Promise<THREE.Object3D | ErrorEvent> {
    const threeStore = setupStore().getState().sceneThree
    const objLoader = new OBJLoader()
    return new Promise(async (resolve, reject) => {
        // var mtlLoader = new MTLLoader();
        // mtlLoader.load(`${path}.mtl`, function (materials) {
        //     materials.preload();
        //     var objLoader = new OBJLoader();
        //     objLoader.setMaterials(materials);
        //     objLoader.load(`${path}.obj`, function (object) {
        //         setupStore().getState()
        //                     resolve(object)
        //                     setupStore().getState()
        //                     // object.scale.set(3, 6, 3)
        //                     threeStore.scene.userData[name] = object;
        //                     threeStore.scene.add(object);
        //                     const box = new THREE.Box3().setFromObject( object );
        //     });
        // });
            objLoader.load(
                path,
                (object) => {
                    object.traverse(child => {
                        //@ts-ignore
                        if (child.material) child.material = material;
                        object.name = name
                    });
                    resolve(object)
                    setupStore().getState()
                    // object.scale.set(3, 6, 3)
                    threeStore.scene.userData[name] = object;
                    threeStore.scene.add(object);
                    const box = new THREE.Box3().setFromObject( object );
                    // console.log(box)
                },
                (xhr) => {
                    console.log((xhr.loaded / xhr.total) * 100 + '% loaded')
                },
                (error) => {
                    console.log('An error happened')
                    reject(error)
                }
            )
        })
    }

    export function addArrowInScene(position: THREE.Vector3, angle?: number) {
        const threeStore = setupStore().getState().sceneThree;
        const object = new THREE.Object3D().copy(threeStore.scene.userData.arrow)
        object.scale.set(0.2, 0.2, 0.2)
        if (angle) object.rotateY(angle)
        object.position.copy(position);
        threeStore.scene.add(object);
        return object
    }
