export function latLonToCartesian(geo: { lat: number, lon: number, radius: number }): THREE.Vector3 {
    const phi = (geo.lat) * (Math.PI / 180);
    const theta = (geo.lon) * (Math.PI / 180);
    const x = geo.radius * Math.cos(phi) * Math.cos(theta)
    const y = geo.radius * Math.cos(phi) * Math.sin(theta)
    const z = geo.radius * Math.sin(phi)
    return new THREE.Vector3(x, y, z)
}

export function latLonToCartesian2(geo: { lat: number, lon: number, radius: number }): THREE.Vector3 {
    const radius1 = 6378137.0
    const radius2 = 6356752.314245

    const phi = (geo.lat) * (Math.PI / 180);
    const theta = (geo.lon) * (Math.PI / 180);
    const x = geo.radius * Math.cos(phi) * Math.cos(theta)
    const y = geo.radius * Math.cos(phi) * Math.sin(theta)
    const z = geo.radius * Math.sin(phi)
    return new THREE.Vector3(x, y, z)
}



export function bearing(startLat:number, startLng:number, destLat:number, destLng:number){
    startLat = toRadians(startLat);
    startLng = toRadians(startLng);
    destLat = toRadians(destLat);
    destLng = toRadians(destLng);

    const y = Math.sin(destLng - startLng) * Math.cos(destLat);
    const x = Math.cos(startLat) * Math.sin(destLat) -
        Math.sin(startLat) * Math.cos(destLat) * Math.cos(destLng - startLng);
    let brng = Math.atan2(y, x);
    brng = toDegrees(brng);
    return (brng + 360) % 360;
}

function toRadians(degrees:number) {
    return degrees * Math.PI / 180;
};

// Converts from radians to degrees.
function toDegrees(radians:number) {
    return radians * 180 / Math.PI;
}
