import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {IApplications} from "../models/IApplications";
import * as THREE from "three";
import {ISceneThree} from "../models/ISceneThree";

const initialState: ISceneThree = {
    scene: new THREE.Scene(),
    camera: new THREE.PerspectiveCamera(45,
        window.innerWidth /
        window.innerHeight ,

        1,
        20
    ),
    renderer: null,
    transformControl: null,
    control: null,
    boxHelper: null,
    mouse: { x: 0, y: 0 },
    canvasPos: { x: 0, y: 0, z: 0 },
    intersectionObjects: [],
    intersects: null,
    msgRecive: null,
    objectConvert: null,
    axisHelper: null,
    WaveSource: null,
    shell: null,
    shadowshell: null,
}

export const sceneThreeSlice = createSlice({
    name: 'application',
    initialState: initialState,
    reducers: {
        setScene(state, action: PayloadAction<any>) {
            state.scene = action.payload;
        },

    }
})

export default sceneThreeSlice.reducer;

export const {setScene} = sceneThreeSlice.actions
