import * as THREE from "three";
import {setupStore} from "../../store/store";

const threeStore = setupStore().getState().sceneThree


export const drawRoad = (path: THREE.Vector3[], name:string) => {
    // ,    
    const closedSpline:any = new THREE.CatmullRomCurve3(path);
    closedSpline.type = 'catmullrom';
    closedSpline.closed = false;

// Set up settings for later extrusion
    const extrudeSettings = {
        steps: 2000,
        bevelEnabled: true,
        extrudePath: closedSpline
    };

    // Define a triangle
    const pts = [], count = 3;
    for (let i = 0; i < count; i++) {
        const l = 0;
        const a = 4 * i / count * Math.PI;
        pts.push(new THREE.Vector2(Math.cos(a) * l, Math.sin(a) * 2));
    }
    const shape = new THREE.Shape(pts);


// Extrude the triangle alon3g the CatmullRom curve
    const geometry = new THREE.ExtrudeGeometry(shape, extrudeSettings);
    const material = new THREE.MeshLambertMaterial({
        color: 0x30DD05,
        wireframe: false,
        transparent: true,
        opacity: 0.8
    });

// Create mesh with the resulting geometry
    const mesh = new THREE.Mesh(geometry, material);
    threeStore.scene.add(mesh);
    threeStore.scene.userData[name] = mesh;
}
