import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {IApplications} from "../models/IApplications";

const initialState: IApplications = {
   yandexMap:{
       map:null,
       cursor:null
   }
}

export const applicationSlice = createSlice({
    name: 'application',
    initialState: initialState,
    reducers: {
        setYandexMap(state, action: PayloadAction<any>) {
            state.yandexMap = action.payload;
        },

    }
})

export default applicationSlice.reducer;

export const {setYandexMap} = applicationSlice.actions