                        const left = mapRef?.current?.width / 135
                        setLeftCursor((startPosition.x + result.pose.translation[0] - 215 - 2.5) * left);
                        setTopCursor((startPosition.z + result.pose.translation[2] + 22 - 2.5) * top);
                        const vector = new THREE.Vector3(0, 0, -1);
                        vector.applyQuaternion(threeStore.camera.quaternion);
                        const angle2 = new Vector2(vector.x, vector.z).normalize().angle()
                        setDirectionTrackingAngle(angle2 * 180 / Math.PI + 90)
                        setAngleCursor(angle2 * 180 / Math.PI + 180)
                        setSlamString(true)
