import {useEffect, useRef, useState} from "react";
import * as THREE from "three";
import {useAppDispatch, useAppSelector} from "./redux";
import {PointerLockControls} from "../components/scripts/PointerLockControls"
import {ICoordinates} from "../models/ICoordinates";
import {addArrowInScene, loadObj} from "../components/scripts/objFilesUtils";
import {routeData} from "../components/Mocks/RouteData";
import {convertTHREEX, convertTHREEY, routerDrawer} from "../components/RouterDrawer/routerUtils";

type UseInitCanvasData = {
    isLoadingData: boolean
};

function useInitCanvas(sceneRef: React.MutableRefObject<any>): UseInitCanvasData {
    const threeStore = useAppSelector((state) => state.sceneThree);
    const dispatch = useAppDispatch();
    const renderer = useRef<any>();
    const player = useRef({height: 1.8, speed: 1, turnSpeed: Math.PI * 0.02});
    const [currentCoordinates, setCurrentCoordinates] = useState<ICoordinates>();
    const [isData, setIsData] = useState<boolean>(false)


    const handleKeyUp = (e: any) => {
        // console.log(e )
        if (e.key === "w") {
            threeStore.scene.userData.control.moveForward(1);
        }
        if (e.key === "s") {
            threeStore.scene.userData.control.moveForward(-1);
        }
        if (e.keyCode === 97) { // A key
            threeStore.scene.userData.control.moveRight(-1);
        }

        if (e.keyCode === 100) { // D key
            threeStore.scene.userData.control.moveRight(1);
        }

        if (e.key === "q") { // left arrow key
            threeStore.scene.userData.control.moveAround(Math.PI / 180, 0)

        }
        if (e.key === "e") { // right arrow key
            threeStore.scene.userData.control.moveAround(-Math.PI / 180, 0)
        }
        if (e.key === "z") { // left arrow key
            threeStore.scene.userData.control.moveAround(0, 1)

        }
        if (e.key === "c") { // right arrow key
            threeStore.scene.userData.control.moveAround(0, -1)
        }
        if (e.key === "r") { // right arrow key
            threeStore.scene.userData.control.setAngleX(3 * Math.PI / 180)
        }
    }


    useEffect(() => {
        threeStore.scene.userData.camera = threeStore.camera;
        //   
        // threeStore.camera.position.set(200, 200, 0);
        // threeStore.camera.position.set(293.948, 40, 17.300000000000004);
        threeStore.camera.position.set(convertTHREEX(612) , 40, convertTHREEY(303));
        // threeStore.camera.rotateX(-3.141592653589793)
        // threeStore.camera.rotateY(0.7777060074217969)
        // threeStore.camera.rotateZ(-3.141592653589793)
        // threeStore.camera.lookAt(new THREE.Vector3(300, 0, 0));
        // threeStore.camera.lookAt(new THREE.Vector3(0, 0, 0));
        console.log(threeStore.scene)
        threeStore.scene.fog = new THREE.FogExp2( 0xaaccff, 0.0007 );

        window.addEventListener("keypress", handleKeyUp)

        // const helperCamera = new THREE.CameraHelper( threeStore.camera );
        // threeStore.scene.add(helperCamera);

        const axesHelper = new THREE.AxesHelper(1000);
        threeStore.scene.add(axesHelper);

        const plane = new THREE.Plane(new THREE.Vector3(0, 1, 0), 0);
        const helper = new THREE.PlaneHelper(plane, 1000, 0xffff00);
        threeStore.scene.add(helper);
        //   
        sceneRef.current.setAttribute("width", 1000);
        sceneRef.current.setAttribute("height", 1000);
        const light = new THREE.AmbientLight( 0x404040 ); // soft white light
        threeStore.scene.add( light );
        //     
        renderer.current = new THREE.WebGLRenderer({
            canvas: sceneRef.current,
            antialias: true,
        });


        //    
        renderer.current.setClearColor(0x00000, 0);

        //         

        threeStore.scene.userData.control = new PointerLockControls(threeStore.camera, document.body);
        console.log(threeStore.scene.userData.control)
        const geometry = new THREE.BoxGeometry( 1, 1, 1 );
        const material = new THREE.MeshNormalMaterial(  );
        const cube = new THREE.Mesh( geometry, material );
        threeStore.scene.add(cube)
        loadObj('/reactar/model.obj', 'arrow', new THREE.MeshBasicMaterial({color: 0xEFF5ED})).then(() => {
        // loadObj('./model.obj', 'arrow', new THREE.MeshBasicMaterial({color: 0xEFF5ED })).then(() => {
            setIsData(true)

        });

        // loadObj('./11e.obj', 'map', new THREE.MeshNormalMaterial())
        //     
        const animate = function () {
            requestAnimationFrame(animate);
            renderer.current.render(threeStore.scene, threeStore.camera, threeStore.scene.userData.control);
            //@ts-ignore
            // renderer.outputEncoding = THREE.sRGBEncoding;
        };

        //  
        animate();
    }, []);

    return {isLoadingData: isData}
}

export default useInitCanvas;
