import React, {useEffect, useRef} from 'react';
import useInitCanvas from "../hooks/useInitCanvas";
import {routerDrawer} from "./RouterDrawer/routerUtils";
import {routeData, routeData2} from "./Mocks/RouteData";

const ArSceneThree = () => {
    const canvasRef = useRef(null);
    // const scene = useRef<THREE.Object3D>(new THREE.Scene()) ;

    const {isLoadingData} = useInitCanvas(canvasRef);

    useEffect(() => {
        if (isLoadingData) {
            const route = routerDrawer({route: routeData})
            // console.log(route)
        }
    }, [isLoadingData])

    return (
        <canvas ref={canvasRef} className={"absolute w-full bottom-[0%] h-[50%] bg-transparent "} id="canvas"></canvas>
    );
};

export default ArSceneThree;
