import {IRoute} from "../../models/IRoutes";
import * as THREE from 'three';
import {BufferGeometry, Float32BufferAttribute, Vector2} from 'three';
import {setupStore} from "../../store/store";
import {addArrowInScene} from "../scripts/objFilesUtils";
import {ContourGeometry} from "./ContourGeometry";
import {drawRoad} from "./drawRoad";

const threeStore = setupStore().getState().sceneThree

interface Props {
    route: IRoute
}

export const convertTHREEX = (value: number) => value * 0.129 + 215
export const convertTHREEY = (value: number) => value * 0.131 - 22

export const routerDrawer = ({route}: Props) => {
    const routeArray = [];
    const path = []
    if (route) {
        for (let i = 0; i < route.reference_points.length; i++) {
            const item = route.reference_points;
            const point1 = new THREE.Vector3(item[i].local.px * 0.129 + 215, 38, item[i].local.py * 0.131 - 22)

            let point2;
            let point3;
            if (item[i + 1]) {
                point2 = new THREE.Vector3(item[i + 1].local.px * 0.129 + 215, 38, item[i + 1].local.py * 0.131 - 22)
                point3 = new THREE.Vector3().subVectors(point2, point1)

                const delta = Math.round(point3.length() / 0.3)
                if (point3.length() < 0.3) continue;
                point3.normalize();
                const sourceAngle = -new Vector2(point1.x, point1.z).angle() + 270 * Math.PI / 180;
                const angle = -new Vector2(point3.x, point3.z).angle() + 270 * Math.PI / 180
                // const angle2 = (point1: any, point2: any) => -Math.atan2(point1.z - point2.z, point1.x - point2.x)+90*Math.PI/180
                path.push(new THREE.Vector3(point1.x, 38, point1.z))
                routeArray.push(addArrowInScene(point1, angle))
                for (let j = 1; j < delta; j++) {
                    routeArray.push(addArrowInScene(point1.addScaledVector(point3, 0.3), angle))
                }
            }

        }
    }

    drawRoad(path, 'road1');

    return routeArray
};

